.cardsWrapper {
  position: relative;
  padding: 20px;
  display: flex;
  justify-content: flex-start;
}

.cardContainer {
  position: relative;
  min-width: 450px;
  min-height: 500px;
  background: linear-gradient(180deg, #f9fafb 0%, #ffffff 100%);
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

/* Заголовок */
.cardTitle {
  font-size: 1rem;
  font-weight: 500;
  color: #111827;
  letter-spacing: 0.3px;
}

/* Сама карточка */
.bankCard {
  border-radius: 16px;
  padding: 24px;
  color: white;
  position: relative;
  overflow: hidden;
  box-shadow: 0 12px 40px rgba(102, 126, 234, 0.4);
  transition: transform 0.2s ease;
  min-height: 200px;
  user-select: none;
  /* не блокируем горизонтальные жесты здесь — сделаем это в viewport */
}

/* Эффект свечения */
.bankCard::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -50%;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  pointer-events: none;
}

.cardTop {
  margin-bottom: 20px;
}

.cardBank {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 6px;
}

.cardType {
  font-size: 0.9rem;
  opacity: 0.9;
  font-weight: 500;
}

.cardMiddle {
  margin-bottom: 20px;
}

.cardNumber {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 2px;
  font-family: 'Courier New', monospace;
}

.cardBottom {
  margin-top: auto;
}

.cardLabel {
  font-size: 0.7rem;
  opacity: 0.8;
  margin-bottom: 4px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.cardValue {
  font-size: 0.9rem;
  font-weight: 600;
  letter-spacing: 1px;
}

/* Контейнер для карусели */
.carouselContainer {
  position: relative;
  width: 100%;
  height: 220px;
  overflow: hidden; /* чтобы показывалась только 1 карта */
}

/* Важно — применяем touch-action к viewport (там где Embla слушает жесты) */
.carouselContainer :global(.mantine-Carousel-viewport) {
  height: 100%;
  touch-action: pan-y; /* разрешаем горизонтальные свайпы, блокируем вертикальную прокрутку при свайпе */
  -webkit-overflow-scrolling: touch;
  pointer-events: auto;
}

/* Слайды тоже должны позволять касания */
.carouselContainer :global(.mantine-Carousel-slide) {
  width: 100%;
  height: 100%;
  flex: 0 0 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: pan-y;
  pointer-events: auto;
}

/* Кнопки управления / индикаторы */
.carouselControl {
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: #666;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.carouselControl:hover {
  background: rgba(255, 255, 255, 1);
  color: #333;
}

.carouselIndicator {
  background: rgba(0, 0, 0, 0.3);
  border: none;
}

.carouselIndicator[data-active] {
  background: #2563eb;
}
